import { createRouter, createWebHistory } from 'vue-router';
import Home from '@/pages/Home.vue';
import Translations from '@/pages/Translations.vue';
import ListPage from '@/pages/ListPage.vue';
import ItemProfile from '@/pages/ItemProfile.vue';

const routes = [
  { path: '/', name: 'Home', component: Home },
  {
    path: '/list/:type/:category?',
    name: 'ItemListPage',
    component: ListPage,
    props: (route) => ({
      type: route.params.type,
      category: route.params.category || '',
    }),
  },
  {
    path: '/items/:category/:id',
    name: 'ItemProfile',
    component: ItemProfile,
  },
  {
    path: '/recipes/:category?/:subCategory?',
    name: 'RecipeListPage',
    component: ListPage,
    props: (route) => ({
      type: 'recipes',
      category: route.params.category || '',
      subCategory: route.params.subCategory || ''
    }),
  },
  { path: '/translations', name: 'Translations', component: Translations },
];

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes,
});

export default router;
