<script setup>
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n';
const { t } = useI18n();
import { getIconPath } from '@/utils/paths.js';

const props = defineProps({
  item: {
    type: Object,
    required: true
  }
})

function isRecipe(type) {
  return type?.includes(' Recipe');
}

function findShopId(itemId) {
  const shop = DATA.SHOPS.data.find(shop =>
    shop.stocks.some(
      stock => stock.id === itemId && isRecipe(stock.type)
    )
  );
  return shop ? shop.id : null;
}

function findGiftRecipeId(itemId) {
  const recipe = DATA.GIFT_RECIPES.find(item =>  item.id === itemId);
  return recipe ? recipe.recipe_source : null;
}

function getRecipeSourceId(itemId) {
  return findShopId(itemId) || findGiftRecipeId(itemId);
}


</script>

<template>
  <div v-if="item && item.how_to_obtain">
    <img :src="getIconPath('/img/icons/others/recipe.png')" class="icon-sm" alt>
    {{ item.how_to_obtain }}
  </div>
  <div v-if="item && getRecipeSourceId(item.id)">
    <img :src="getIconPath('/img/icons/others/recipe.png')" class="icon-sm" alt>
    {{ t(getRecipeSourceId(item.id)) }}
  </div>
</template>

