<script setup>
import { useI18n } from 'vue-i18n';
import { getImagePath, getIconPath } from '@/utils/paths.js';
import ShopList from '@/components/ShopList.vue';
import HowToObtainRecipe from '@/components/HowToObtainRecipe.vue';
import { DATA } from '@/utils/constants.js';
import { ref, computed, onMounted, onBeforeUnmount, inject  } from 'vue';
import IconElement from '@/components/IconElement.vue';
import IconRarity from '@/components/IconRarity.vue';

const { t } = useI18n();
const props = defineProps({ itemId: String });

function getItemFromId(itemId) {
  if (!itemId) return null;
  const parts = itemId.split('.');
  if (parts.length < 2) return null;
  
  const key = parts[1].toUpperCase(); // e.g., "WEAPONS"
  console.log(DATA[key])
  const list = DATA[key]?.data;
  if (!list) return null;

  return list.find(item => item.id === itemId) || null;
}

const item = computed(() => getItemFromId(props.itemId));
</script>

<template>
  <div v-if="item">
    
    <div href="#" class="flex items-center bg-[#fef]">
      <img :src="getImagePath(item.id)" class="icon-md" alt />
      <h2>{{ t(item.id) }}</h2>
    </div>
    <IconElement :element="item.element" />
    <IconRarity :rarity="item.rarity" />
    <HowToObtainRecipe v-if="item.crafter" :item="item" />
    <ShopList :itemId="item.id" />

    <div v-for="(recipe, index) in item.recipes" :key="index">
      <table class="data-table">
        <thead>
          <tr>
            <th colspan="2">
              <img :src="getIconPath(`/img/icons/stats/stat-life-${item.crafter}.png`)" class="icon-sm" :alt="item.crafter">
            {{ recipe.crafting_level }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(material, index) in recipe.materials" :key="index">
            <td>
              <img :src="getImagePath(material.id)" class="icon-sm" alt>
              {{ t(material.id) }}
              <div class="ml-3">
                <ShopList :itemId="material.id" />
              </div>
            </td>
            <td class="border text-right">{{ material.quantity }}</td>
          </tr>
        </tbody>
      </table>
    </div>

  </div>

</template>
