    <table class="data-table" v-for="(recipe, index) in item.recipes" :key="index">
      <thead>
        <tr v-if="recipe.rank">
          <th colspan="2" class="text-center" scope="col">
             <img 
              :src="getIconPath(`/img/icons/rarity/rank-${recipe.rank}.png`)" :alt="recipe.rank" class="icon-sm" />
