import { ref, watch, onMounted, onBeforeUnmount } from 'vue'

export function useInfiniteLoader(source, itemsPerLoad = 20, useScroll = true) {
  const displayedItems = ref([])
  let index = 0
  let loading = false

  const loadMore = () => {
    if (loading) return
    loading = true

    const next = source.value.slice(index, index + itemsPerLoad)
    displayedItems.value.push(...next)
    index += itemsPerLoad

    loading = false
  }

  const loadAll = () => {
    displayedItems.value = [...source.value]
    index = source.value.length
  }

  const onScroll = () => {
    if (window.innerHeight + window.scrollY >= document.body.offsetHeight - 300) {
      loadMore()
    }
  }

  watch(source, () => {
    displayedItems.value = []
    index = 0
    loadMore()
  }, { immediate: true })

  onMounted(() => {
    if (useScroll) window.addEventListener('scroll', onScroll)
  })
  onBeforeUnmount(() => {
    if (useScroll) window.removeEventListener('scroll', onScroll)
  })

  return { displayedItems, loadMore, loadAll }
}
