<script setup>
import { itemsState } from '@/store/states.js'
import { DATA } from '@/utils/constants.js'
import { ref, computed } from 'vue'
import { applyCategory, applyFilters, applySort, FILTER_OPTIONS } from '@/composables/useFilters.js'
import { useI18n } from 'vue-i18n'
import { getIconPath } from '@/utils/paths.js'
import { useInfiniteLoader } from '@/composables/useInfiniteLoader'
import BaseModal from '@/components/BaseModal.vue'
import MaterialModal from '@/components/MaterialModal.vue'
import ItemCard from '@/components/ItemCard.vue'
import FilterOptions from '@/components/FilterOptions.vue';

const { t } = useI18n()

const { type, category, subCategory } = defineProps({
  type: String,
  category: String,
  subCategory: String
})

const showModal = ref(false)
const selectedMaterial = ref(null)

function openMaterialModal(material) {
  selectedMaterial.value = material
  showModal.value = true
}

const items = computed(() => {
  const key = type.toUpperCase()
  return DATA[key]?.data || []
})

const categoryItems = computed(() => applyCategory(items.value, category) || [])

const subCategoryItems = computed(() => {
  if (!subCategory) return categoryItems.value
  return categoryItems.value.filter(
    item => item.sub_category?.toLowerCase() === subCategory.toLowerCase()
  )
})

const filteredItems = computed(() => applyFilters(
  subCategoryItems.value,
  itemsState.selectedFilters,
  itemsState.filterMode,
  FILTER_OPTIONS
))

const selectedSort = ref('asc')

const sortedItems = computed(() => {
  if (!selectedSort.value) return filteredItems.value
  return applySort(filteredItems.value, selectedSort.value)
})

const { displayedItems, loadMore, loadAll } = useInfiniteLoader(sortedItems, 20, true)

const selectedCols = ref(4)
const gridClass = computed(() => `grid-cols-${selectedCols.value} list-decimal`)

const selectedImprov = ref(0)

const improvIcons = ['/img/icons/others/improv-1.png', '/img/icons/others/improv-2.png']
const improvLabels = ['None', '?', '? and ??']

const displayedImprov = computed(() => improvIcons.slice(0, selectedImprov.value))

</script>

<template>
  <div data-ui="content-list">
    <FilterOptions :type="type" />

    <select v-model="selectedSort" class="border p-1 rounded mb-4">
      <option :value="null">Sort (none)</option>
      <option value="asc">Name A  Z</option>
      <option value="desc">Name Z  A</option>
    </select>

    <div class="mb-4">
      <label>Select number of columns:</label>
      <select v-model="selectedCols" class="border p-1 rounded mb-4">
        <option v-for="n in 6" :key="n" :value="n">{{ n }}</option>
      </select>
    </div>
        <div class="mb-4">
      <label
        v-for="(label, index) in improvLabels"
        :key="index"
        :for="`improv-recipe-${index}`"
        class="cursor-pointer border p-2">
        <input
          type="radio"
          name="improv-recipe"
          :id="`improv-recipe-${index}`"
          :value="index"
          v-model="selectedImprov"
          class="mr-2" />
        {{ label }}
      </label>
    </div>

    <h1 class="text-[30px]">
      {{ t(`menu.${type}`) }} ({{ items.length }})
      <span v-if="category">
        - {{ t(`menu.${type}.${category.replace(/-/g, '_')}`) }} ({{ categoryItems.length }})
      </span>
      <span v-if="itemsState.selectedSubCategory">
        - {{ itemsState.selectedSubCategory }} ({{ subCategoryItems.length }})
      </span>
      <span v-if="itemsState.selectedFilters.length > 0">
        - Filters: {{ itemsState.selectedFilters }} ({{ filteredItems.length }})
      </span>
    </h1>

    <ol :class="gridClass">
      <li
        v-for="(icon, index) in displayedImprov"
        :key="index"
        class="bg-yellow-100 p-2 mb-2 rounded">
        <img :src="getIconPath(icon)" alt="">
        Chemistry/Cooking/Art
      </li>
      <ItemCard v-for="item in displayedItems" :key="item.id" :item="item" :type="type"  @open-material-modal="openMaterialModal" />
    </ol>

    <BaseModal v-model="showModal">
      <MaterialModal v-if="selectedMaterial" :itemId="selectedMaterial.id" />
    </BaseModal>

    <div v-if="displayedItems.length < filteredItems.length">
      <button
        @click="loadMore"
        class="w-full cursor-pointer p-4 bg-blue-500 text-white rounded mt-4">
        Load More
      </button>
      <button
        @click="loadAll"
        class="w-full cursor-pointer p-4 bg-pink-500 text-white rounded mt-4">
        Load All
      </button>
    </div>
  </div>
</template>
