<script setup>
import { ref } from 'vue'
import { RouterLink } from 'vue-router'
import { getImagePath, itemIdToRoute } from '@/utils/paths.js'
import HowToObtainRecipe from '@/components/HowToObtainRecipe.vue'
import ShopList from '@/components/ShopList.vue'
import StatsTable from '@/components/StatsTable.vue'
import RecipeTable from '@/components/RecipeTable.vue'
import IconElement from '@/components/IconElement.vue'
import IconRarity from '@/components/IconRarity.vue'
import IconRank from '@/components/IconRank.vue'
import IconTimeOfDay from '@/components/IconTimeOfDay.vue'
import { itemsState } from '@/store/states.js'
import { useI18n } from 'vue-i18n'
const { t } = useI18n()
const props = defineProps({
  item: Object,
  type: String
})

function showCheckbox(item) {
  return (
    item.crafter ||
    props.type === "monsters" ||
    props.type === "strangelings" ||
    props.type === "characters" ||
    props.type === "gatherables" ||
    props.type === "mounts"
  )
}
const emit = defineEmits(['open-material-modal'])
</script>

<template>
  <li :data-version="item.version" class="border bg-white p-1 relative ml-8">
    
    <div v-if="showCheckbox(item)" class="absolute top-2 right-2">
      <input type="checkbox" :value="item.id" class="w-6 h-6 accent-blue-500 cursor-pointer" />
       <img src="/img/icons/others/recipe.png" alt="" class="icon-sm">
    </div>

    <RouterLink :to="itemIdToRoute(item.id)"
      class="flex items-center bg-[#fef]">
      <img :src="getImagePath(item.id)" class="icon-md" alt />
      <h2>{{ t(item.id) }}</h2>
    </RouterLink>

    <IconElement :element="item.element" />
    <IconRarity :rarity="item.rarity" />
    <IconRank :rank="item.rank" />
    <IconTimeOfDay v-if="type === 'monsters'" :timeOfDay="item.time_of_day" />

    <HowToObtainRecipe v-if="item.crafter" :item="item" />
    <ShopList :itemId="item.id" />
    <StatsTable v-if="item.baseStat && type !== 'recipes'" :item="item" />
    <RecipeTable v-if="item.recipes" :item="item" @open-material-modal="material => emit('open-material-modal', material)" />

  </li>
</template>
