<script setup>
import DebugConsole from '@/layouts/DebugConsole.vue';
import { itemsState } from '@/store/states.js'
import { DATA } from '@/utils/constants.js'
import { ref, computed, onMounted, onBeforeUnmount, inject, onActivated } from 'vue'
import { applyCategory, applyFilters, applySort, FILTER_OPTIONS } from '@/composables/useFilters.js'
import { useI18n } from 'vue-i18n'
import { getImagePath, getRoutePath, itemIdToRoute, getIconPath } from '@/utils/paths.js'
import ShopList from '@/components/ShopList.vue'
import HowToObtainRecipe from '@/components/HowToObtainRecipe.vue'
import RecipeTable from '@/components/RecipeTable.vue'
import ItemSkills from '@/components/ItemSkills.vue'
import AgedSkills from '@/components/AgedSkills.vue'
import InOtherLanguages from '@/components/InOtherLanguages.vue'
import StatsTable from '@/components/StatsTable.vue'
import IconElement from '@/components/IconElement.vue'
import IconRarity from '@/components/IconRarity.vue'
import IconRank from '@/components/IconRank.vue'
import IconTimeOfDay from '@/components/IconTimeOfDay.vue'
import { RouterLink, useRoute } from 'vue-router';
import ShoppingTable from '../components/ShoppingTable.vue'
import LifeType from '../components/LifeType.vue'
const { t } = useI18n()
const route = useRoute()

const { type, category } = defineProps({
  type: String,
  category: String
})

const item = computed(() => {
  if (itemsState.selectedItem) return itemsState.selectedItem

  const category = route.params.category
  const id = route.params.id.replace(/-/g, '_');
  const itemId = `items.${category}.${id}`
  console.log(category, id, itemId)
  const key = category.toUpperCase();

  return DATA[key].data.find(item => item.id === itemId)
})

</script>

<template>
  <DebugConsole :data="item" label="item" />
  <div class="text-red-600 text-4xl font-bold">
    work in progress
  </div>

  <h1 class="text-5xl font-bold tracking-tight mb-4">{{ t(item.id) }}</h1>
  <img :src="getImagePath(item.id)" class="" alt />

  <LifeType />

  <IconElement v-if="item.element" :element="item.element" />
  <IconRarity :rarity="item.rarity" />
  <IconRank :rank="item.rank" />
  <IconTimeOfDay v-if="item.time_of_day" :timeOfDay="item.time_of_day" />

  <HowToObtainRecipe v-if="item.crafter" :item="item" />
  <ShopList :itemId="item.id" />
  <StatsTable v-if="item.baseStat" :item="item" />
  <ShoppingTable :item="item" />
  <RecipeTable v-if="item.recipes" :item="item" @material-clicked="openMaterialModal" />
  <ItemSkills v-if="item.baseStat" :item="item" />
  <AgedSkills v-if="item.baseStat" :item="item" />
  <InOtherLanguages :itemId="item.id" />
</template>
