<script setup>
import { ref, watch } from 'vue'
import { useI18n } from 'vue-i18n'
import { DATA } from '@/utils/constants.js'
import { getImagePath, itemIdToRoute } from '@/utils/paths.js'

const { t } = useI18n()
const query = ref('')
const suggestions = ref([])

const allItems = DATA.ALL_ITEMS

watch(query, (queryValue) => {
  if (!queryValue) {
    suggestions.value = []
    return
  }

  const search = queryValue.trim()
  suggestions.value = allItems.filter(item =>
    t(item.id).includes(search) || item.id.includes(search)
  ).slice(0, 10)
})

</script>

<template>
  <div class="relative w-80">
    <input
      type="text"
      v-model="query"
      placeholder="Search items..."
      class="w-full px-3 py-2 border rounded"
    />

    <ul
      v-if="suggestions.length"
      class="absolute z-10 w-full bg-white border rounded mt-1 max-h-80 overflow-auto shadow-lg"
    >
      <li
        v-for="item in suggestions"
        :key="item.id"
        class="px-3 py-2 cursor-pointer hover:bg-blue-100"
      >
        <RouterLink
          :to="itemIdToRoute(item.id)"
          class="flex items-center gap-2"
          @click="() => { query = ''; suggestions = [] }"
        >
          <img :src="getImagePath(item.id)" class="icon-sm" alt />
          {{ t(item.id) }}
        </RouterLink>
      </li>
    </ul>
  </div>
</template>
