<script setup>
import { computed, ref, watch } from 'vue'
import { itemsState } from '@/store/states.js'
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n';
import { getRoutePath, getIconPath } from '@/utils/paths.js';
const { t } = useI18n();

const { type } = defineProps({
  type: String,
})

const mode = ref('AND')


const selectedFilterIds = computed({
  get: () => itemsState.selectedFilters,
  set: (val) => { itemsState.selectedFilters = val }
})

function toggleFilter(option) {
  const index = selectedFilterIds.value.indexOf(option.id)
  if (index === -1) {
    selectedFilterIds.value.push(option.id)
  } else {
    selectedFilterIds.value.splice(index, 1)
  }
}

const options = computed(() => {
  if (!type) return DATA.FILTER_OPTIONS
  return DATA.FILTER_OPTIONS.filter(option =>
    !option.for || option.for.includes(type)
  )
})

function resetFilters() {
  selectedFilterIds.value = []
  mode.value = 'AND'
}

// watch(() => itemsState.selectedCategory, () => {
//   itemsState.selectedFilters = []
//   itemsState.filterMode = 'AND'
// })

</script>

<template>
  <div class="border">
    <div class="text-red-600 text-4xl font-bold">
      filterOptions (Not working yet)
    </div>
    <button @click="resetFilters" class="p-2 bg-red-300 rounded">
      Reset
    </button>
    <label>
      <input type="radio" value="AND" v-model="mode" /> AND
    </label>
    <label>
      <input type="radio" value="OR" v-model="mode" /> OR
    </label>
    <label>
      <input type="radio" value="NOT" v-model="mode" /> NOT
    </label>

    <ul class="flex flex-wrap gap-2">
      <li
        v-for="option in options"
        :key="option.id"
        @click="toggleFilter(option)"
        :class="`p-2 cursor-pointer ${selectedFilterIds.includes(option.id) ? 'bg-yellow-300 rounded' : ''}`">
        <img :src="getIconPath(option.icon_id)" class="icon-sm" alt>
        {{ t(option.id) }}
      </li>
    </ul>
  </div>
</template>
