<script setup>
import { getImagePath, getRoutePath, itemIdToRoute, getIconPath } from '@/utils/paths.js'
import { useI18n } from 'vue-i18n'
const { t } = useI18n()

const offsets = {
  swords: [30, 70],
  greatswords: [40, 80],
  bows: [30, 60],
  staves: [20, 60],
  sticks: [30, 70],
}


function getCells(item) {
  const cells = [];
  const rarity = item.rarity;
  const onlyLegendary = item.only_legendary;
  const baseStat = item.baseStat;
  const slot = item.category.toLowerCase();

  const stats = {
    swords: [baseStat + 30, baseStat + 70],
    greatswords: [baseStat + 40, baseStat + 80],
    bows: [baseStat + 30, baseStat + 60],
    staves: [baseStat + 20, baseStat + 60],
    sticks: [baseStat + 30, baseStat + 70],
  };

  // Before
  if (onlyLegendary) cells.push('', '')
  else if (rarity === 4 || rarity === 5) cells.push('', '')
  else if (rarity === 3) cells.push('')

  // BaseStat (middle case)
  if (rarity !== 5 || !onlyLegendary) cells.push(baseStat)

  // After
  if (rarity === 5 && !onlyLegendary) cells.push(...stats[slot])
  else if (rarity === 4) cells.push(...stats[slot])
  else if (rarity === 5) cells.push('', '')
  else if (rarity === 3) cells.push(...stats[slot], '')
  else if (rarity < 3 && rarity !== 0) cells.push(...stats[slot], '', '')
  else if (rarity === 0) cells.push('', '', '', '')

  // BaseStat (end case for rarity 5 + legendary)
  if (onlyLegendary) cells.push(baseStat)

  return cells
}

const props = defineProps({
  item: Object,
});

function getRank(item, recipeRank) {
  const recipe = item.recipes.find(recipe => recipe.rank.toLowerCase() === recipeRank.toLowerCase())
  return recipe;
}



</script>

<template>
  <table class="data-table">
    <thead>
      <tr>
        <th>
          <img src="/img/icons/stats/stat-attack.png" class="icon-sm" alt="">
        </th>
        <th>Normal</th>
        <th>
          <img :src="getIconPath('/img/icons/rarity/rarity-tier-2.png')" class="icon-sm" alt>
        </th>
        <th>
          <img :src="getIconPath('/img/icons/rarity/rarity-tier-3.png')" class="icon-sm" alt>
        </th>
        <th>
          <img :src="getIconPath('/img/icons/rarity/rarity-tier-4.png')" class="icon-sm" alt>
        </th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td>
          <img :src="getIconPath('/img/icons/rarity/rank-ordinary.png')" class="icon-sm" alt>
        </td>
        <td>
          <!-- <div v-if="item.rarity < 3">{{ item.baseStat }}</div> -->
        </td>
        <td>
          <!-- <div v-if="item.rarity < 3 && item.rarity !== 0">
            {{ item.baseStat + offsets[item.category.toLowerCase()][0] }}
          </div> -->
        </td>
        <td>
          <!-- <div v-if="item.rarity < 3 && item.rarity !== 0">{{ item.baseStat + offsets[item.category.toLowerCase()][1] }}</div> -->
        </td>
        <td>
          {{ getRank(item, 'Ordinary')?.rank }}
        </td>
      </tr>
      <tr>
        <td>
          <img :src="getIconPath('/img/icons/rarity/rank-fine.png')" class="icon-sm" alt>
        </td>
        <td></td>
        <td></td>
        <td></td>
        <td>
          {{ getRank(item, 'Fine')?.rank }}
        </td>
      </tr>
      <tr>
        <td>
          <img :src="getIconPath('/img/icons/rarity/rank-notable.png')" class="icon-sm" alt>
        </td>
        <td></td>
        <td></td>
        <td></td>
        <td>
          {{ getRank(item, 'Notable')?.rank }}
        </td>
      </tr>
      <tr>
        <td>
          <img :src="getIconPath('/img/icons/rarity/rank-supreme.png')" class="icon-sm" alt>
        </td>
        <td></td>
        <td></td>
        <td></td>
        <td>
          {{ getRank(item, 'Supreme')?.rank }}
        </td>
      </tr>
      <tr>
        <td>
          <img :src="getIconPath('/img/icons/rarity/rank-legendary.png')" class="icon-sm" alt>
        </td>
        <td></td>
        <td></td>
        <td></td>
        <td>
          {{ getRank(item, 'Legendary')?.rank }}
        </td>
      </tr>
    </tbody>
  </table>
</template>
