import MENU_ITEMS from '@/assets/data/app/menu-items.json';
import GIFTS from '@/assets/data/gifts.json';
import ARMORS from '@/assets/data/armors.json';
import CHARACTERS from '@/assets/data/characters.json';
import CONSUMABLES from '@/assets/data/consumables.json';
import GATHERABLES from '@/assets/data/gatherables.json';
import LOCATIONS from '@/assets/data/locations.json';
import MATERIALS from '@/assets/data/materials.json';
import MONSTERS from '@/assets/data/monsters.json';
import MOUNTS from '@/assets/data/mounts.json';
import OBJECTS from '@/assets/data/objects.json';
import SHIELDS from '@/assets/data/shields.json';
import SHOPS from '@/assets/data/shops.json';
import STRANGELINGS from '@/assets/data/strangelings.json';
import TOOLS from '@/assets/data/tools.json';
import WEAPONS from '@/assets/data/weapons.json';
import GIFT_RECIPES from '@/assets/data/others/gift-recipes.json';
import TRANSLATIONS from '@/assets/data/app/i18n.json';
import ICONS from '@/assets/data/app/icons.json';
import FILTER_OPTIONS from '@/assets/data/app/filter-options.json';
import SORT_OPTIONS from '@/assets/data/app/sort-options.json';

const RECIPE_TYPES = {
  weapons: WEAPONS,
  tools: TOOLS,
  shields: SHIELDS,
  armors: ARMORS,
  potions: { data: CONSUMABLES.data.filter(item => item.category === 'Potions') },
  meals: { data: CONSUMABLES.data.filter(item => item.category === 'Meals') },
  objects: OBJECTS,
  materials: MATERIALS,
}

function extractRecipesFromFile(file) {
  const items = file.data || []
  return items.filter(item => Array.isArray(item.recipes) && item.recipes.length)
}

const RECIPES = {
  meta: {},
  data: Object.entries(RECIPE_TYPES)
  .flatMap(([category, file]) =>
    extractRecipesFromFile(file).map(item => ({
      ...item,
      recipe_source: getShopIdForRecipe(item.id),
      category,
      sub_category: item.category
    }))
  )
};

function getShopIdForRecipe(itemId) {
  function isRecipe(type) {
    return type?.includes(' Recipe');
  }

  const shop = SHOPS.data.find(shop =>
    shop.stocks.some(
      stock => stock.id === itemId && isRecipe(stock.type)
    )
  );
  return shop ? shop.id : null;
}


function newArray() {
  const data = MATERIALS.data;
  const dataFind = GIFT_RECIPES;
  return data.map(item => {
     const crafter = dataFind.find(craft => craft.id === item.id);
    return {
      ...item,
      crafter: crafter ? crafter.crafter : null
    };
  });
}

export const ITEM_DATA = {
  ARMORS,
  CHARACTERS,
  CONSUMABLES,
  GATHERABLES,
  LOCATIONS,
  MATERIALS,
  MONSTERS,
  MOUNTS,
  OBJECTS,
  SHIELDS,
  SHOPS,
  STRANGELINGS,
  TOOLS,
  WEAPONS,
}

const ALL_ITEMS = Object.values(ITEM_DATA).flatMap(obj => obj.data)

export const DATA = {
  MENU_ITEMS,
  ARMORS,
  CHARACTERS,
  CONSUMABLES,
  GATHERABLES,
  GIFTS,
  LOCATIONS,
  MATERIALS,
  MONSTERS,
  MOUNTS,
  OBJECTS,
  SHIELDS,
  SHOPS,
  STRANGELINGS,
  TOOLS,
  WEAPONS,
  TRANSLATIONS,
  RECIPES,
  ICONS,
  GIFT_RECIPES,
  FILTER_OPTIONS,
  SORT_OPTIONS,
  ALL_ITEMS
}
