<script setup>
import { itemsState } from '@/store/states.js'
import { DATA } from '@/utils/constants.js'
import { ref } from 'vue'

const props = defineProps({
  data: { type: [Object, Array, String, Number, Boolean, null], default: null },
  label: { type: String, default: 'Show in console' }
})

function logData() {
  console.log(props.data)
}

function getTranslation(itemId) {
  const item =  DATA.TRANSLATIONS.find(item => item.id === itemId)
  return item.ja
}
</script>

<template>
  <div class="p-3 rounded-xl border border-dashed border-zinc-400 dark:border-zinc-600 bg-zinc-50 dark:bg-zinc-800/40">
    <strong>{{ data.id }}</strong>
    <h2>{{ getTranslation( data.id ) }}</h2>
    <div class="flex justify-end items-center">
      <button
        @click="logData"
        class="px-3 py-1 text-lg rounded bg-blue-500 text-white hover:bg-blue-600">
        {{ label }}
      </button>
    </div>

  </div>
</template>
