<script setup>
import { RouterLink, useRoute } from 'vue-router';
import { useI18n } from 'vue-i18n';
import { watch, computed } from 'vue';
import { DATA } from '@/utils/constants.js';
import { getRoutePath, getIconPath } from '@/utils/paths.js';
import SortOptions from '@/components/SortOptions.vue';

import { itemsState } from '@/store/states.js';

const { t } = useI18n();
const route = useRoute();

const menuItems = DATA.MENU_ITEMS;

const activeMenu = computed(() => {
  const cleanPath = getRoutePath(route.path);
  return menuItems.find(menu => cleanPath.startsWith(menu.route));
}); 

function selectSubCategory(subId) {
  itemsState.selectedSubCategory = subId;
}

function isActive(routePath) {
  return getRoutePath(route.path).startsWith(routePath)
}

watch(
  activeMenu,
  (menu) => {
    itemsState.activeMenu = menu || null
    itemsState.selectedSort = null
    itemsState.selectedFilters = []
    itemsState.selectedCategory = null
    itemsState.selectedSubCategory = null
  },
  { immediate: true }
);

</script>

<template>
  <nav id="app-nav" class="">

    <ul id="main-menus" class="">
      <li 
        v-for="menu in menuItems" :key="menu.id" 
        :class="['menu-item', { 'is-active': activeMenu?.id === menu.id }]"
      >
        <RouterLink
          :to="getRoutePath(menu.route)"
          class="menu-item-link">
          <img :src="getIconPath(menu.icon_id)" class="icon-md" alt />
          <span class="link-label">{{ t(menu.id) }}</span>
        </RouterLink>

        <ul v-if="menu.categories?.length" class="menu-categories">
          <li 
            v-for="category in menu.categories" :key="category.id" 
            :class="['menu-category-item', { 'is-active': isActive(category.route) }]"
          >
            <RouterLink
              :to="getRoutePath(category.route)"
              class="menu-item-link">
              <img :src="getIconPath(category.icon_id)" class="icon-sm" alt />
              <span class="link-label">{{ t(category.id) }}</span>
            </RouterLink>

            <ul v-if="category.sub_categories?.length" class="menu-sub-categories">
              <li 
                v-for="sub in category.sub_categories" :key="sub.id" 
                :class="['menu-sub-category-item', { 'is-active': isActive(sub.route) }]"
              >
                <RouterLink
                  :to="getRoutePath(sub.route)"
                  @click.native.prevent="() => selectSubCategory(sub.id)"
                  class="menu-item-link">
                  <img :src="getIconPath(sub.icon_id)" class="icon-sm" alt />
                  <!-- {{ t(sub.id) }} -->
                  <span class="link-label">{{ t(sub.id) }}</span>
                </RouterLink>
              </li>
            </ul>
            
          </li>
        </ul>
      </li>
      <li class="menu-item">
        <RouterLink
          :to="getRoutePath('/')"
          class="menu-item-link">
          <img :src="getIconPath('icons.others.gifts')" class="icon-md" alt />
          <span class="link-label">{{ t('menu.gifts') }}</span>
        </RouterLink>
      </li>
      <!-- <li>
        <RouterLink
          :to="getRoutePath('translations')"
          class="block p-[10px]">
          translations
        </RouterLink>
      </li> -->
    </ul>

        
    <!-- <SortOptions /> -->
    

  </nav>
</template>
